import React, { Component } from 'react';

import logo from '../assets/imgs/logo.svg';
import hamburger from '../assets/imgs/icon-hamburger.svg';

export default class NavBar extends Component {

    render() {
        return (
            <div className="nav-bar">
                <div className="flex main-container nav-bar-subcontainer">
                    <div className="flex logo-menu-container">
                        <div className="logo-container">
                            <img src={logo} />
                        </div>
                        <div className="menu-btn">
                            <img src={hamburger} />
                        </div>
                    </div>
                    <div className="full-menu">
                        <div className="flex align-center ">
                            <div className="home-button">home</div>
                            <div>about</div>
                        </div>
                        <div className="flex center align-center up-contact-btn"><span>contact us</span></div>
                    </div>
                </div>
            </div>
        );
    }
}
